@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  display: flex;
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  /* justify-content: center; */
  line-height: normal;
  text-align: center;
  font-size: 0;
  gap: 10px;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  padding: 10px;
  margin-left: 0;
  color: #111;
  background-color: #fff;
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  line-height: 1.22;
  text-decoration: none;
  vertical-align: middle;
  border: 2px solid #111;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tui-pagination .tui-page-btn:hover {
  background-color: var(--violet-color);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: var(--black-header-color);
  border-color: var(--black-header-color);
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--black-header-color);
}
.tui-pagination .tui-first,
.tui-pagination .tui-prev {
  background-color: var(--yellow-color);
}
.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid var(--black-header-color);
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid var(--black-header-color);
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  width: 44px;
  height: 44px;
  padding: 0;
  font-size: 0;
  line-height: 44px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid var(--black-header-color);
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled {
  background-color: var(--black-header-color);
}
.tui-pagination .tui-is-disabled:hover {
  background: var(--black-header-color);
  cursor: default;
}
.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: var(--black-header-color);
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: var(--violet-color);
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-pagination .tui-last,
.tui-pagination .tui-next {
  background-color: var(--yellow-color);
}

.tui-pagination .tui-is-disabled,
.tui-pagination .tui-is-disabled {
  background-color: var(--black-header-color);
}
