h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header-page {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-container {
  width: 100%;
  z-index: 1000;
  height: 64px;
  border: 1.5px solid var(--black-body-color);
  background-color: var(--white-color);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.header-logo {
  letter-spacing: -.56px;
  align-items: center;
  gap: 8px;
  padding-top: 18px;
  padding-bottom: 18px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
}

.header-logo-icon-bookshelf {
  stroke: #000;
}

.header-nav, .header-item-link {
  align-items: center;
  display: flex;
}

.header-item-link:hover, .header-item-link:focus {
  transform: scale(1.2);
}

.header-nav-list {
  letter-spacing: -.56px;
  text-transform: uppercase;
  align-items: baseline;
  gap: 24px;
  font-weight: 500;
  line-height: 1.28;
  display: none;
}

.header-wrap {
  align-items: center;
  gap: 14px;
  display: flex;
}

.icon-shoping-list {
  fill: currentColor;
}

.user-authorization {
  width: 182px;
  background: var(--violet-color);
  border-radius: 36px;
  justify-content: space-between;
  align-items: center;
  gap: 9px;
  padding: 4px 12px;
  display: none;
}

.user-authorization-title {
  color: var(--white-color);
  letter-spacing: -.44px;
  font-size: 22px;
  font-weight: 700;
  line-height: normal;
}

.header-active {
  background-color: var(--yellow-color);
  transition: background-color var(--transition-dur-and-func);
  border-radius: 36px;
  padding: 8px 16px;
}

.header-active .header-item-link, .header-active .mobile-menu-link {
  color: var(--black-header-color);
  transition: font-weight var(--transition-dur-and-func), color var(--transition-dur-and-func);
  font-weight: 700;
}

.mobile-button-menu, .mobile-button-menu-close {
  min-height: 28px;
  min-width: 28px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .mobile-button-menu {
    display: none;
  }
}

.menu-container {
  width: 100%;
  height: 100%;
  z-index: 999;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  flex-direction: column;
  padding-top: 64px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .mobile-button-menu {
  color: var(--black-body-color);
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .link {
