  stroke: var(--violet-color);
}

.mobile-button-menu, .mobile-button-menu-close {
  display: none;
}

.mobile-button-menu, .mobile-button-menu-close.is-open {
  display: block;
}

.mobile-button-menu.is-open {
  display: none;
}

